<template>
    <el-container id="app">
        <el-header class="app-header">
            <div class="app-header-left">
                <img src="./assets/logo.png" class="logo"/>
                <span>企贷金服</span>
            </div>
            <div @click="jumpUserInfo" style="cursor: pointer;">登录/注册</div>
        </el-header>
        <div class="mainBody">
            <router-view/>
        </div>
    </el-container>
</template>

<script>
    export default {
        name: 'App',
        data() {
            return {

            }
        },
        mounted() {
            console.log('==========', this.$router);
        },
        methods: {
            jumpUserInfo() {
                this.$router.push({ path: '/companyAuth' });
            }
        }
    };
</script>

<style lang="less">
    .app-header {
        background-color: #f5f5f5;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 20px;
    }
    .logo {
        width: 60px;
        height: 60px;
    }
    .app-header-left {
        display: flex;
        align-items: center;
        span {
            font-size: 16px;
            font-weight: bold;
        }
    }
    .mainBody {
        padding-top: 10px;
    }
</style>


