import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

export default new Router({
    routes: [
        {
            path: '/',
            name: 'Home',
            component: () => import('@/views/Home')
        },
        {
          path: '/products',
          name: 'Products',
          component: () => import('@/views/Products')
        },
        {
            path: '/companyAuth',
            name: 'CompanyAuth',
            component: () => import('@/views/CompanyAuth')
        },
        {
            path: '/progressQuery',
            name: 'ProgressQuery',
            component: () => import('@/views/ProgressQuery')
        },
        {
            path: '/contractQuery',
            name: 'ContractQuery',
            component: () => import('@/views/ContractQuery')
        },
        {
            path: '/mineLoans',
            name: 'MineLoans',
            component: () => import('@/views/MineLoans')
        },
        {
            path: '/withDraw',
            name: 'WithDraw',
            component: () => import('@/views/WithDraw')
        },
        {
            path: '/repayment',
            name: 'Repayment',
            component: () => import('@/views/Repayment')
        },
        {
            path: '/404',
            name: '404',
            component: () => import('@/views/404')
        },
    ]
});



